* {
  color: #d1d1d1;
  margin: 0;
  padding: 0;
  border: none;
  outline: none;
  text-decoration: none;
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  scroll-behavior: smooth;
}

body {
  height: 100vh;
}

#root {
  height: calc(100% - 20px);
}

button {
  cursor: pointer;
  border-radius: 7px;
}

::placeholder {
  color: #63676e;
}